class InputWrapper {
  constructor(node) {
    this.node = node;
  }

  get value() {
    return this.node.value;
  }

  set value(value) {
    if (this.node.value !== value) {
      this.node.value = value;
    }
  }

  observe(cb) {
    this.node.addEventListener('input', cb);
  }
}

class RadioWrapper extends InputWrapper {
  observe(cb) {
    this.node.addEventListener('change', cb);
  }
}

class Link {
  constructor(engine, node, evaluate) {
    const wrapper = new InputWrapper(node);
    const expr = engine.link(evaluate, {
      value: wrapper.value,
      observe: value => wrapper.value = value
    });

    wrapper.observe(()=> {
      engine.root.setState(()=> {
        expr.value = wrapper.value;
      });
    });
  }
}

export default Link;



